  --gradient-blue: linear-gradient(
    45deg,
    rgb(111, 190, 233) 0%,
    hsla(173, 48%, 83%, 1) 100%
  );
  --transparent-white: hsla(0, 0%, 100%, 0.4);
  --lighter-transparent-white: hsla(0, 0%, 100%, 0.25);
  --inter: 'Inter', sans-serif;
  font-family: var(--inter);
}

body {
  display: flex;
  height: 100svh;
  overflow: hidden;
  background: var(--gradient-blue);
}

#wrapper {
  display: flex;
}

/* Sidebar */

#sidebar {
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-width: 350px;
  background-color: var(--transparent-white);

  hr {
    width: 100%;
    border: none;
    border-bottom: solid 1px var(--transparent-white);
  }
}

#top-sidebar {
  display: flex;
  justify-content: space-between;
  border-bottom: solid 2px var(--transparent-white);
}

#title {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 20px;

  img {
    height: 50px;
    width: 50px;
  }

  h1 {
    font-size: 2.2rem;
  }
}

#icon-close {
  display: flex;
  align-items: center;
  margin: 5px 0 0 0;
  padding: 0 20px;
  cursor: pointer;
  display: none;

  i {
    font-size: 2.5rem;
    color: #156dd8;
  }
}

.autocomplete-container {
  position: relative;
  width: 80%;
  margin: 0 auto;
}

.geoapify-autocomplete-input,
.geoapify-autocomplete-items {
  border-radius: 10px;
}

#searchbar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 310px;
  height: 85px;
  background-color: var(--transparent-white);
  border-radius: 5px;
  margin: 0 auto;
}

/* Toggle Switch */

.switch {
  position: relative;
  display: inline-block;
  width: 65px;
  height: 34px;

  input {
    opacity: 0;
    width: 0;
    height: 0;
  }
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 35px;
  background-color: #cbecfb;
  transition: 0.4s;
  border: solid 2px white;

  &::before {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    content: '°C';
    color: white;
    height: 26px;
    width: 26px;
    left: 3px;
    bottom: 2px;
    border-radius: 50%;
    background-color: #156dd8;
    transition: 0.4s;
  }
}

input:checked + .slider {
  background-color: #fcebeb;

  &::before {
    content: '°F';
    transform: translateX(30px);
    background-color: #ed4134;
  }
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

/* Main Container */

#container {
  padding: 30px;
  flex-grow: 1;
  overflow-y: auto;
}

/* Top Widgets Block */

#top-widgets {
  display: flex;
  justify-content: space-between;
  align-items: center;

  i {
    color: white;
  }
}

#burger-menu {
  cursor: pointer;
  visibility: hidden;
}

/* Weather Container */

#weather {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
}

/* Today */

#today {
  display: flex;
  flex-direction: column;
  max-width: 625px;
  align-items: center;
}

/* Current Weather Block*/

#current-weather {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-width: 390px;
  padding: 30px;

  h1 {
    font-size: 6rem;

    span {
      padding: 0 0 0 30px;
    }
  }

  h5 {
    font-weight: 300;
  }
}

/* Hours Weather */

#hours-weather {
  display: flex;
  justify-content: center;
  gap: 20px;
  min-width: 390px;
  height: 137px;
  padding: 20px;
  font-weight: 300;
  border-radius: 10px;
  border: solid 1px white;
  background-color: var(--lighter-transparent-white);
}

.hour-card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hour {
  font-size: 0.75rem;
  text-transform: lowercase;
}

.hour-temp {
  font-size: 0.875rem;
}

/* Prevsions Weather Block */

#previsions-weather {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 356px;
  height: 551px;
  gap: 5px;
  padding: 20px;
  margin: 50px 0 0 0;
  border-radius: 10px;
  border: solid 1px white;
  background-color: var(--lighter-transparent-white);

  hr {
    width: 100%;
    border: none;
    border-bottom: solid 1px var(--transparent-white);

    &:last-of-type {
      display: none;
    }
  }
}

.previsions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.875rem;
  font-weight: 300;
}

.previsions-data {
  display: flex;
  align-items: center;
  gap: 15px;
}

/* Media Queries */

@media screen and (max-width: 1035px) {
  #sidebar {
    display: none;
  }

  #burger-menu {
    visibility: visible;
  }
}

@media screen and (min-width: 1035px) {
  body {
    width: unset !important;
  }

  #wrapper {
    width: unset !important;
  }

  #container {
    display: block !important;
  }

  #sidebar {
    display: flex !important;
    width: unset !important;
  }

  #icon-close {
    display: none !important;
  }

  #burger-menu {
    visibility: hidden !important;
  }
}
