___CSS_LOADER_EXPORT___.push([module.id, `.geoapify-autocomplete-input {
    outline: none;
    line-height: 36px;
    height: 36px;
    padding: 0 31px 0 7px;
    width: calc(100% - 40px);

    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;

    font-size: 14px;
}

.geoapify-autocomplete-items {
    position: absolute;
    top: calc(100% + 2px);
    left: 0;
    right: 0;

    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    overflow: hidden;

    box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.1);
    z-index: 99;
}

.geoapify-autocomplete-items div {
    padding: 10px;
    cursor: pointer;
}

.geoapify-autocomplete-items div:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

.geoapify-autocomplete-items .active {
    background-color: rgba(0, 0, 0, 0.1);
}

.geoapify-autocomplete-item {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.geoapify-autocomplete-item .icon {
    display: inline-block;
    width: 40px;
    height: 24px;
    color: #aaa;
}

.geoapify-autocomplete-item .icon.emoji {
    color: unset;
    font-size: 20px;
    opacity: 0.9;
}

.geoapify-close-button {
    position: absolute;
    right: 5px;
    top: 0;

    height: 100%;
    display: none;
    align-items: center;
}

.geoapify-close-button.visible {
    display: flex;
}

.geoapify-close-button {
    color: rgba(0, 0, 0, 0.4);
    cursor: pointer;
}

.geoapify-close-button:hover {
    color: rgba(0, 0, 0, 0.6);
}

.geoapify-autocomplete-items .main-part .non-verified {
    color:  #ff4848;
}

.geoapify-autocomplete-items .secondary-part {
    margin-left: 10px;
    font-size: small;
    color: rgba(0, 0, 0, 0.6);
}`, "",{"version":3,"sources":["webpack://./node_modules/@geoapify/geocoder-autocomplete/styles/round-borders.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,iBAAiB;IACjB,YAAY;IACZ,qBAAqB;IACrB,wBAAwB;;IAExB,oCAAoC;IACpC,kBAAkB;;IAElB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,qBAAqB;IACrB,OAAO;IACP,QAAQ;;IAER,sBAAsB;IACtB,oCAAoC;IACpC,kBAAkB;IAClB,gBAAgB;;IAEhB,+CAA+C;IAC/C,WAAW;AACf;;AAEA;IACI,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;IACrB,WAAW;IACX,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,MAAM;;IAEN,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,yBAAyB;AAC7B","sourcesContent":[".geoapify-autocomplete-input {\n    outline: none;\n    line-height: 36px;\n    height: 36px;\n    padding: 0 31px 0 7px;\n    width: calc(100% - 40px);\n\n    border: 1px solid rgba(0, 0, 0, 0.2);\n    border-radius: 5px;\n\n    font-size: 14px;\n}\n\n.geoapify-autocomplete-items {\n    position: absolute;\n    top: calc(100% + 2px);\n    left: 0;\n    right: 0;\n\n    background-color: #fff;\n    border: 1px solid rgba(0, 0, 0, 0.1);\n    border-radius: 5px;\n    overflow: hidden;\n\n    box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.1);\n    z-index: 99;\n}\n\n.geoapify-autocomplete-items div {\n    padding: 10px;\n    cursor: pointer;\n}\n\n.geoapify-autocomplete-items div:hover {\n    background-color: rgba(0, 0, 0, 0.1);\n}\n\n.geoapify-autocomplete-items .active {\n    background-color: rgba(0, 0, 0, 0.1);\n}\n\n.geoapify-autocomplete-item {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n}\n\n.geoapify-autocomplete-item .icon {\n    display: inline-block;\n    width: 40px;\n    height: 24px;\n    color: #aaa;\n}\n\n.geoapify-autocomplete-item .icon.emoji {\n    color: unset;\n    font-size: 20px;\